:root {
  background: url('images/back2.jpg'); //Azure;
}

.App {
  text-align: center;
  font-family: $font-family;
}

.cal_h {
  color: White;
  font-size: larger;
  font-weight: bold;
  margin: 10px;
}

.cal_b {  // calendar body
  display: grid;  // container
  grid-template-columns: repeat(3, minmax(160px, 1fr));
  grid-template-rows:    repeat(4, minmax(200px, 1fr));
  @media (max-width: 600px) {
    grid-template-columns: repeat(2, minmax(160px, 1fr));
    grid-template-rows:    repeat(6, minmax(200px, 1fr));
  }
  @media (min-width: 1200px) {
    grid-template-columns: repeat(4, minmax(160px, 1fr));
    grid-template-rows:    repeat(3, minmax(200px, 1fr));
  }
  grid-gap: 1rem;
  margin:  0 40px 40px 40px;  // t r b l
  //font-size: 1.8vw;
  //@include fluid-font(12, 20, 600, 1000);
  font-size: calc( 12 * 1px + (20 - 12)*((100vw - 600 * 1px)/(1000 - 600)) );  // responsive
  @media (max-width: 600px)  { font-size: 12px; }  // min font-size
  @media (min-width: 1000px) { font-size: 20px; }  // max font-size
}

section {
  margin: 0;
}

.mon {
  max-width:  280px;
  min-height: 240px;
  align-self: center;
  header {  // month head
    color: White;
    background: $hdr-color;
    border-radius: 10px 10px 0 0;
    font-size: larger;
    font-weight: bold;
  }
  footer {  // month footer
    line-height: 2rem;
    background: $bdr-color;
  }
  .mon_b {  // month body
    //position: absolute;
    display: grid;  // container
    grid-template-columns: repeat(7, minmax(10px, 1fr));
    grid-template-rows:    repeat(7, 1fr);
    //grid-gap: .1em;
    //width: 200px;
    //height: 200px;
    margin: 0;
    padding: 0;
    border: 1px solid $bdr-color;
    background: White;

    li {
      display: block;
      position: relative;
      justify-content: center;
      //align-items: center;
      min-height: 24px;
      height: 3vh;

      &:nth-child(7n+1) {  // 1st col, Sun
        color: red;
        font-weight: bold;
      }
      &:nth-child(7n+7) {  // 7th col, Sat
        color: blue;
        font-weight: bold;
      }
      &:nth-child(-n+7) {  // 7th and below, week header
        font-weight: bold;
        background: Lavender;
      }
      &:not(:nth-child(-n+7)):hover {
        cursor: pointer;
        border-radius: 50%;
        background: lightgreen;
      }
    }

    .m_pv, .m_nx {
      font-size: smaller;
      font-style: italic;
      opacity: .3;
    }

  }
}


